@import url(https://fonts.googleapis.com/css2?family=ZCOOL+QingKe+HuangYou&display=swap);
.cell {
  height: 20px;
  width: 20px;
  border: 1px solid skyblue;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.cell:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.weight {
  content: url(data:image/png;base64,);
  background-size: 20px 20px;
}

.weight-animated {
  content: url(data:image/png;base64,);
  background-size: 20px 20px;
  -webkit-animation: weighted 0.2s linear;
          animation: weighted 0.2s linear;
}

.wall {
  background-color: black;
  border: 1px solid black;
}

.wall-animated {
  background-color: black;
  border: 1px solid black;
  -webkit-animation: wall 0.2s linear;
          animation: wall 0.2s linear;
}

.unvisited,
.unvisited-animated {
  background-color: aliceblue;
}

.visited {
  background-color: rgb(153, 153, 238);
  border-radius: 0%;
}

.visited-animated {
  -webkit-animation: visit-animation 0.5s linear;
          animation: visit-animation 0.5s linear;
  background-color: rgb(153, 153, 238);
  border-radius: 0%;
}

.path {
  background-color: yellow;
  border: 1px solid yellow;
}

.path-animated {
  background-color: yellow;
  border: 1px solid yellow;
  -webkit-animation: pathAnimation 0.2s linear;
          animation: pathAnimation 0.2s linear;
}

.source {
  content: url(data:image/png;base64,);
  background-size: 19px 19px;
  background-color: yellow;
  border: 1px solid yellow;
}

.destination {
  content: url(data:image/png;base64,);
  background-size: 20px 20px;
  background-color: yellow;
  border: 1px solid yellow;
}

@-webkit-keyframes weighted {
  0% {
    -webkit-transform: scale(1.8);
            transform: scale(1.8);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes weighted {
  0% {
    -webkit-transform: scale(1.8);
            transform: scale(1.8);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@-webkit-keyframes wall {
  0% {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
    background-size: 30%;
    background-color: grey;
    border-color: grey;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-size: 100%;
    background-color: black;
    border-color: black;
  }
}

@keyframes wall {
  0% {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
    background-size: 30%;
    background-color: grey;
    border-color: grey;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-size: 100%;
    background-color: black;
    border-color: black;
  }
}

@-webkit-keyframes visit-animation {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    background-color: rgba(0, 0, 66, 0.75);
    border-radius: 100%;
  }

  50% {
    background-color: rgb(153, 153, 238);
  }

  75% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgb(153, 153, 238);
    border-radius: 0%;
  }
}

@keyframes visit-animation {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    background-color: rgba(0, 0, 66, 0.75);
    border-radius: 100%;
  }

  50% {
    background-color: rgb(153, 153, 238);
  }

  75% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgb(153, 153, 238);
    border-radius: 0%;
  }
}

@-webkit-keyframes pathAnimation {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    background-color: rgb(248, 248, 186);
    border-radius: 100%;
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: yellow;
    border: 1px solid yellow;
    border-radius: 0%;
  }
}

@keyframes pathAnimation {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    background-color: rgb(248, 248, 186);
    border-radius: 100%;
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: yellow;
    border: 1px solid yellow;
    border-radius: 0%;
  }
}

.row {
  display: flex;
}

.grid {
  background-color: aliceblue;
  position: relative;
  top: 100px;
  bottom: 20px;
  border: 22px solid black;
  border-radius: 10px 10px;
  padding: 0;
  margin: auto;
}

.controlPanel {
  display: flex;
  justify-content: center;
  margin: auto;
  position: absolute;
  left: 15px;
  top: 20px;
  z-index: 1;
}

.panel {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  background-color: rgb(255, 255, 255);
  border: 2px solid rgb(24, 23, 94);
  border-radius: 10px 10px;
  margin: 0 5px;
  padding: 0 15px 5px 5px;
  max-height: 29px;
  max-width: 220px;
  overflow: hidden;
  z-index: 2;
  transition: all 0.3s;
}

.panel:hover {
  max-height: 500px;
  height: 100%;
  max-width: 500px;
}

.panel button,
input,
label {
  margin: 5px 5px 0 5px;
  font-family: "ZCOOL QingKe HuangYou", cursive;
  font-size: 20px;
  font-weight: 500;
}

.input-box {
  margin: 5px 5px 0 5px;
  height: 25px;
}

.input-box input[type="range"] {
  width: 110px;
}

/* @font-face {
  font-family: Oswald;
  src: url(../Fonts/MochiyPopPOne-Regular.ttf);
} */
.panel button {
  background-color: rgb(24, 23, 94);
  border: 1px solid white;
  border-radius: 10px 10px;
  color: white;
  min-height: 40px;
  transition: all 0.2s;

  min-width: 60px;
  width: 100%;
}

.panel button:enabled:hover {
  background-color: white;
  border-color: rgb(24, 23, 94);
  color: rgb(24, 23, 94);
  cursor: pointer;
}

.panel button:enabled:active {
  background-color: rgba(64, 63, 126, 0.356);
  border-color: rgb(107, 106, 206);
}

.panel button:disabled {
  border: 1px solid rgb(24, 23, 94);
  background-color: rgb(24, 23, 94);
  color: #666666;
  cursor: no-drop;
}

.panel-title {
  width: calc(100% - 15px);
  text-align: center;
  padding-bottom: 10px;
  border-bottom: 1px solid rgb(24, 23, 94);
  transition: all 1.3s;
}

.inline-block * {
  margin: 0 2px;
  padding: 0;
}

.inline-block img {
  height: 30px;
  margin: 0 4px;
  width: auto;
}

.inline-block label {
  min-width: 140px;
  white-space: nowrap;
  margin-right: 15px;
}

.inline-block {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-top: 10px;
}

body {
  background-color: white;
}

